/*=================================================================*/


/*                      DARK MODE                              
/*=================================================================*/

body.dark {
    color: #FFF;
    background-color: $primaryBackgroundColor;
    .section-title {
        color: #FFF;
        &:before {
            background: url('/images/dots-bg-light.svg');
        }
    }
    .bg-dark {
        background: $secondaryBackgroundColor !important;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: #FFF;
    }
    blockquote {
        background: #5f5ca6;
    }
    .triangle-top:before {
        border-bottom-color: $secondaryBackgroundColor;
    }
    .timeline.exp .timeline-container::after,
    .timeline.edu .timeline-container::after {
        background: $secondaryBackgroundColor;
    }
    .portfolio-filter li button {
        color: #FFF;
    }
    a:hover {
        color: #FFF;
    }
    .portfolio-filter li button.current {
        color: $colorpink;
    }
    .blog-item .details h4.title a {
        color: #FFF;
        &:hover {
            color: $colorpink;
        }
    }
    .contact-info {
        background-image: url('/images/map-light.png');
    }
    .white-popup {
        background: $secondaryBackgroundColor;
    }
    footer.footer {
        border-top: solid 1px rgba(255, 255, 255, 0.1);
    }
    .footer {
        font-size: 14px;
        padding-left: 10px;
        padding-top: 10px;
    }
    .content-blog,
    .blog-page-section {
        background: #353353;
    }
    .bg-white {
        background-color: $secondaryBackgroundColor !important;
    }
    .blog-standard .meta li a,
    .blog-standard .title a,
    .comment-author .fn {
        color: #fff;
    }
    .blog-page-section .blog-pagination ul li {
        background-color: $secondaryBackgroundColor !important;
    }
    .blog-page-section .blog-single {
        background-color: $secondaryBackgroundColor !important;
    }
}